import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import generateSeats from './redux/actions/generateSeats';

const Button = styled.span`
  background: #59b0d2;
  display: inline-block;
  border-radius: 5px;
  color: black;
  text-align: center;
  vertical-align: middle;
  padding: 5px 15px 5px 15px;
  font-size: 40px;
  color: white;
  align-self: center;
  margin-top: 30px;
  cursor: pointer;
`;

function GenerateButton(props) {
  return (
    <Button onClick={props.generateSeats}>Generate</Button>
  );
}

GenerateButton.propTypes = {
  generateSeats: PropTypes.func.isRequired,
};

const mapDispatchToProps = dispatch => ({
  generateSeats: () => dispatch(generateSeats()),
});

export default connect(undefined, mapDispatchToProps)(GenerateButton);




