import _ from 'lodash';

const getFreeSeats = (floor) => {
  const freeSeats = [];
  floor.forEach((row, i) => {
    row.forEach((seat, j) => {
      if (seat === 1) {
        freeSeats.push([i, j]);
      }
    });
  });
  return freeSeats;
};

const generateSeats = ({ floor, students }) => {
  const freeSeats = getFreeSeats(floor);
  const shuffled = _.shuffle([...students.male, ...students.female]);
  return shuffled.map((name, i) => [...freeSeats[i], name]);
};

export default (state = [], action) => {
  switch (action.type) {
    case 'GENERATE_SEATS':
      return generateSeats(action);
    default:
      return state;
  }
};



