const students = {
  male: [
    'John', 'Carl', 'Eric', 'Paul', 'Tom', 'Joe',
  ],
  female: [
    'Mary', 'Susie', 'Ann', 'May',
  ],
};

export default (state = students, action) => {
  switch (action.type) {
    case 'UPDATE_STUDENTS':
      return {
        ...state,
        [action.sex]: action.students,
      };
    default:
      return state;
  }
};



