import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import styled from 'styled-components';
import _ from 'lodash';
import Seat from './Seat';
import Border from './Border';
import toggleSeat from './redux/actions/toggleSeat';

const Container = Border.extend`
  align-self: flex-start;
  margin-right: 20px;
  min-width: 744px;
`;

const Row = styled.div`
  font-size: 0;
`;

function Floor(props) {
  return (
    <Container>
      {
        _.range(props.rows).map(i => (
          <Row key={i} >
            {_.range(props.cols).map(j => <Seat key={j} i={i} j={j} />)}
          </Row>
        ))
      }
    </Container>
  );
}

Floor.propTypes = {
  rows: PropTypes.number.isRequired,
  cols: PropTypes.number.isRequired,
};

const mapStateToProps = state => ({
  rows: state.floor.length,
  cols: state.floor[0].length,
});

const mapDispatchToProps = dispatch => ({
  toggleSeat: (i, j) => dispatch(toggleSeat(i, j)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Floor);




