import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Border from './Border';
import toggleSeat from './redux/actions/toggleSeat';

const Box = Border.extend`
  display: inline-block;
  line-height: 30px;
  height: 30px;
  width: 60px;
  font-size: 14px;
  background: ${props => (props.isSeat ? '#cce6ff' : 'white')};
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
  color: ${props => (props.isEmptySeat ? 'white' : 'slategray')};
  cursor: pointer;
`;

function Seat(props) {
  return (
    <Box
      isSeat={props.isSeat}
      isEmptySeat={props.isEmptySeat}
      onClick={() => props.toggleSeat(props.i, props.j)}
    >
      {props.label}
    </Box>
  );
}

Seat.propTypes = {
  i: PropTypes.number.isRequired,
  j: PropTypes.number.isRequired,
  toggleSeat: PropTypes.func.isRequired,
  isSeat: PropTypes.bool.isRequired,
  isEmptySeat: PropTypes.bool.isRequired,
  label: PropTypes.string.isRequired,
};

const mapStateToProps = (state, { i, j }) => {
  const nameInfo = state.seats.find(seat => seat[0] === i && seat[1] === j);
  const isSeat = state.floor[i][j] === 1;
  const emptySeatLabel = isSeat ? 'Empty' : '';
  const isEmptySeat = !nameInfo;
  return {
    isSeat,
    isEmptySeat,
    label: isEmptySeat ? emptySeatLabel : nameInfo[2],
  };
};

const mapDispatchToProps = dispatch => ({
  toggleSeat: (i, j) => dispatch(toggleSeat(i, j)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Seat);



