import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import styled from 'styled-components';
import updateStudents from './redux/actions/updateStudents';

const TextArea = styled.textarea`
  resize: none;
  min-width: 100px;
  height: 482px;
  margin-right: 7px;
`;

function Students(props) {
  const handleChange = (event) => {
    const students = event.target.value.split('\n')
      .map(student => student.trim())
      .filter(student => student);

    props.updateStudents(students, props.sex);
  };

  return (
    <TextArea onChange={handleChange} defaultValue={props.students} />
  );
}

Students.propTypes = {
  updateStudents: PropTypes.func.isRequired,
  sex: PropTypes.string.isRequired,
  students: PropTypes.string.isRequired,
};

const mapStateToProps = (state, { sex }) => ({
  students: state.students[sex].join('\n'),
});

const mapDispatchToProps = dispatch => ({
  updateStudents: (students, sex) => dispatch(updateStudents(students, sex)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Students);



